'use client'
import { useState } from "react"

import { Avatar } from "@/registry/new-york-v4/ui/avatar"
import { Button } from "@/registry/new-york-v4/ui/button"
import { Input } from "@/registry/new-york-v4/ui/input"
import {
  Bot,
  Loader2,
  Mic,
  Paperclip,
  Plus,
  Search,
  Send,
  User
} from "lucide-react"


import { Bar, BarChart, CartesianGrid, Legend, ResponsiveContainer, Tooltip, XAxis, YAxis } from 'recharts'
import { ShipsListProps } from "./chat-input"
import { useEntityStore } from "@/store/EntityStore"

export default function ChatMain({ ships }: ShipsListProps) {
  const {setShip}= useEntityStore()
  const [messages, setMessages] = useState([])
  const [inputValue, setInputValue] = useState("")
  const [isLoading, setIsLoading] = useState(false)
  const [showWelcome, setShowWelcome] = useState(true)

  const sendMessage = async () => {
    if (!inputValue.trim() || isLoading) return

    const userMessage = {
      role: "user",
      content: inputValue.trim(),
      timestamp: new Date().toISOString()
    }

    // Add user message and hide welcome screen
    setMessages(prev => [...prev, userMessage])
    setShowWelcome(false)
    setIsLoading(true)

    // Clear input
    const messageToSend = inputValue.trim()
    setInputValue("")

    try {
      const response = await fetch('http://localhost:8000/chat/', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          message: messageToSend,
          conversation_history: messages
        })
      })

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`)
      }

      const data = await response.json()

      const assistantMessage = {
        role: "assistant",
        content: data.response,
        timestamp: data.timestamp,
        tool_calls: data.tool_calls
      }

      setMessages(prev => [...prev, assistantMessage])
    } catch (error) {
      console.error('Error sending message:', error)
      const errorMessage = {
        role: "assistant",
        content: "Sorry, I encountered an error while processing your message. Please try again.",
        timestamp: new Date().toISOString(),
        isError: true
      }
      setMessages(prev => [...prev, errorMessage])
    } finally {
      setIsLoading(false)
    }
  }

  const handleKeyPress = (e) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault()
      sendMessage()
    }
  }

  const startNewChat = () => {
    setMessages([])
    setShowWelcome(true)
    setInputValue("")
  }

  const handleQuickAction = (action) => {
    setInputValue(action)
    setShowWelcome(false)
  }

  // Function to check if message has component reliability tool calls
  const hasReliabilityToolCall = (toolCalls) => {
    if (!toolCalls || !Array.isArray(toolCalls)) return false
    return toolCalls.some(tool => tool.name === 'get_component_reliability')
  }

  // Function to extract chart data from tool calls
  const getReliabilityChartData = (toolCalls) => {
    if (!toolCalls || !Array.isArray(toolCalls)) return null

    const reliabilityTool = toolCalls.find(tool => tool.name === 'get_component_reliability')
    if (!reliabilityTool || !reliabilityTool.result) return null

    const result = reliabilityTool.result

    // Handle single component result
    if (result.data && result.data.reliability_score !== undefined) {
      return [{
        name: result.data.nomenclature || result.data.component_name || 'Component',
        reliability: (result.data.reliability_score * 100).toFixed(2),
        reliabilityDecimal: result.data.reliability_score
      }]
    }

    // Handle multiple component results
    if (result.data && result.data.results && Array.isArray(result.data.results)) {
      return result.data.results
        .filter(item => item.reliability !== null && item.reliability !== undefined)
        .map(item => ({
          name: item.nomenclature || 'Unknown',
          reliability: (item.reliability * 100).toFixed(2),
          reliabilityDecimal: item.reliability
        }))
    }

    return null
  }

  // Custom tooltip for the chart
  const CustomTooltip = ({ active, payload, label }) => {
    if (active && payload && payload.length) {
      const data = payload[0].payload
      return (
        <div className="bg-card border border-border rounded-lg p-3 shadow-lg">
          <p className="font-medium">{`${label}`}</p>
          <p className="text-primary">
            {`Reliability: ${data.reliability}%`}
          </p>
          <p className="text-sm text-muted-foreground">
            {`Score: ${data.reliabilityDecimal.toFixed(4)}`}
          </p>
        </div>
      )
    }
    return null
  }

  return (
    <div className="flex-1 flex flex-col h-screen">
      {/* Header */}
      <header className="bg-card border-b border-border px-6 py-4">
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-4">
            <h1 className="text-xl font-semibold text-card-foreground">AI Chat</h1>
            {!showWelcome && (
              <Button variant="outline" size="sm" onClick={startNewChat}>
                New Chat
              </Button>
            )}
          </div>
          {/* <div className="flex items-center gap-3">
            <Button className="bg-primary hover:bg-primary/90 text-primary-foreground">
              <Sparkles className="w-4 h-4 mr-2" />
              Upgrade
            </Button>
            <Button variant="ghost" size="icon">
              <HelpCircle className="w-4 h-4" />
            </Button>
            <Button variant="ghost" size="icon">
              <div className="w-4 h-4 bg-muted rounded"></div>
            </Button>
            <Avatar className="w-8 h-8">
              <AvatarImage src="/placeholder.svg?height=32&width=32" />
              <AvatarFallback>EC</AvatarFallback>
            </Avatar>
          </div> */}
        </div>
      </header>

      {/* Chat Content */}
      <div className="flex-1 flex flex-col overflow-hidden">
        {showWelcome ? (
          /* Welcome Content */
          <div className="flex-1 flex items-center justify-center p-6">
            <div className="max-w-2xl w-full text-center space-y-8">
              <div className="space-y-4">
                <h2 className="text-4xl font-bold text-foreground">Welcome to Netra</h2>
                <p className="text-lg text-muted-foreground">
                  Get started by asking a question about reliability or any other task.
                </p>
              </div>

              {/* Action Cards */}
              <div className="grid grid-cols-2 gap-4 max-w-lg mx-auto">
                <Button
                  variant="outline"
                  className="h-16 flex items-center justify-between p-4 hover:bg-accent hover:text-accent-foreground border-border bg-card"
                  onClick={() => handleQuickAction("Write a professional email")}
                >
                  <div className="flex items-center gap-3">
                    <div className="w-8 h-8 bg-accent rounded-full flex items-center justify-center">
                      <span className="text-accent-foreground">‚úèÔ∏è</span>
                    </div>
                    <span className="font-medium">Write copy</span>
                  </div>
                  <Plus className="w-4 h-4 text-muted-foreground" />
                </Button>

                <Button
                  variant="outline"
                  className="h-16 flex items-center justify-between p-4 hover:bg-accent hover:text-accent-foreground border-border bg-card"
                  onClick={() => handleQuickAction("What is the reliability of GT 1 over 10 hours?")}
                >
                  <div className="flex items-center gap-3">
                    <div className="w-8 h-8 bg-accent rounded-full flex items-center justify-center">
                      <span className="text-accent-foreground">üìä</span>
                    </div>
                    <span className="font-medium">Check reliability</span>
                  </div>
                  <Plus className="w-4 h-4 text-muted-foreground" />
                </Button>

                <Button
                  variant="outline"
                  className="h-16 flex items-center justify-between p-4 hover:bg-accent hover:text-accent-foreground border-border bg-card"
                  onClick={() => handleQuickAction("Create a professional avatar")}
                >
                  <div className="flex items-center gap-3">
                    <div className="w-8 h-8 bg-accent rounded-full flex items-center justify-center">
                      <span className="text-accent-foreground">üë§</span>
                    </div>
                    <span className="font-medium">Create avatar</span>
                  </div>
                  <Plus className="w-4 h-4 text-muted-foreground" />
                </Button>

                <Button
                  variant="outline"
                  className="h-16 flex items-center justify-between p-4 hover:bg-accent hover:text-accent-foreground border-border bg-card"
                  onClick={() => handleQuickAction("Write Python code to analyze data")}
                >
                  <div className="flex items-center gap-3">
                    <div className="w-8 h-8 bg-accent rounded-full flex items-center justify-center">
                      <span className="text-accent-foreground">üíª</span>
                    </div>
                    <span className="font-medium">Write code</span>
                  </div>
                  <Plus className="w-4 h-4 text-muted-foreground" />
                </Button>
              </div>
            </div>
          </div>
        ) : (
          /* Chat Messages */
          <div className="flex-1 overflow-y-auto p-6">
            <div className="max-w-4xl mx-auto space-y-6">
              {messages.map((message, index) => (
                <div key={index} className={`flex gap-4 ${message.role === 'user' ? 'justify-end' : 'justify-start'}`}>
                  {message.role === 'assistant' && (
                    <Avatar className="w-8 h-8 mt-1">
                      <Bot className="w-4 h-4" />
                    </Avatar>
                  )}

                  <div className={`max-w-3xl ${message.role === 'user' ? 'order-first' : ''}`}>
                    <div className={`rounded-lg p-4 ${message.role === 'user'
                      ? 'bg-primary text-primary-foreground ml-auto'
                      : message.isError
                        ? 'bg-destructive/10 text-destructive border border-destructive/20'
                        : 'bg-muted'
                      }`}>
                      <div className="whitespace-pre-wrap">{message.content}</div>

                      {/* Reliability Chart */}
                      {message.role === 'assistant' && hasReliabilityToolCall(message.tool_calls) && (
                        <div className="mt-6">
                          {(() => {
                            const chartData = getReliabilityChartData(message.tool_calls)
                            if (chartData && chartData.length > 0) {
                              return (
                                <div className="bg-card rounded-lg border border-border p-4">
                                  <h3 className="text-lg font-semibold mb-4 text-card-foreground">
                                    Reliability Distribution
                                  </h3>
                                  <ResponsiveContainer width="100%" height={300}>
                                    <BarChart
                                      data={chartData}
                                      margin={{
                                        top: 20,
                                        right: 30,
                                        left: 20,
                                        bottom: 5,
                                      }}
                                    >
                                      <CartesianGrid strokeDasharray="3 3" className="stroke-border" />
                                      <XAxis
                                        dataKey="name"
                                        className="text-muted-foreground"
                                        tick={{ fontSize: 12 }}
                                      />
                                      <YAxis
                                        className="text-muted-foreground"
                                        tick={{ fontSize: 12 }}
                                        label={{ value: 'Reliability (%)', angle: -90, position: 'insideLeft' }}
                                      />
                                      <Tooltip content={<CustomTooltip />} />
                                      <Legend />
                                      <Bar
                                        dataKey="reliability"
                                        name="Reliability (%)"
                                        fill="hsl(var(--primary))"
                                        radius={[4, 4, 0, 0]}
                                      />
                                    </BarChart>
                                  </ResponsiveContainer>
                                  <div className="mt-3 text-sm text-muted-foreground">
                                    * Reliability scores are shown as percentages. Higher values indicate better reliability.
                                  </div>
                                </div>
                              )
                            }
                            return null
                          })()}
                        </div>
                      )}

                      {/* Tool Calls Display */}
                      {message.tool_calls && message.tool_calls.length > 0 && (
                        <div className="mt-4 pt-4 border-t border-border/20">
                          <div className="text-sm text-muted-foreground mb-2">Tool calls:</div>
                          {message.tool_calls.map((tool, toolIndex) => (
                            <div key={toolIndex} className="bg-background/50 rounded p-3 mb-2 text-sm">
                              <div className="font-medium text-foreground">{tool.name}</div>
                              <div className="text-muted-foreground mt-1">
                                Arguments: {JSON.stringify(tool.arguments, null, 2)}
                              </div>
                              {tool.result && (
                                <div className="text-muted-foreground mt-2">
                                  <details className="cursor-pointer">
                                    <summary className="hover:text-foreground">View result</summary>
                                    <pre className="mt-2 text-xs overflow-x-auto bg-background/80 p-2 rounded">
                                      {JSON.stringify(tool.result, null, 2)}
                                    </pre>
                                  </details>
                                </div>
                              )}
                            </div>
                          ))}
                        </div>
                      )}
                    </div>

                    <div className="text-xs text-muted-foreground mt-1 px-4">
                      {new Date(message.timestamp).toLocaleTimeString()}
                    </div>
                  </div>

                  {message.role === 'user' && (
                    <Avatar className="w-8 h-8 mt-1">
                      <User className="w-4 h-4" />
                    </Avatar>
                  )}
                </div>
              ))}

              {isLoading && (
                <div className="flex gap-4 justify-start">
                  <Avatar className="w-8 h-8 mt-1">
                    <Bot className="w-4 h-4" />
                  </Avatar>
                  <div className="bg-muted rounded-lg p-4">
                    <div className="flex items-center gap-2">
                      <Loader2 className="w-4 h-4 animate-spin" />
                      <span className="text-muted-foreground">Thinking...</span>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>
        )}
      </div>

      {/* Chat Input */}
      <div className="p-6 border-t border-border">
        <div className="max-w-4xl mx-auto">
          <div className="bg-card border border-border rounded-lg p-4">
            <div className="flex items-center gap-3">
              <Input
                value={inputValue}
                onChange={(e) => setInputValue(e.target.value)}
                onKeyPress={handleKeyPress}
                placeholder="Ask about component reliability or any other question..."
                className="flex-1 border-0 focus-visible:ring-0 text-base bg-transparent"
                disabled={isLoading}
              />
              <Button
                variant="ghost"
                size="icon"
                onClick={sendMessage}
                disabled={isLoading || !inputValue.trim()}
              >
                {isLoading ? (
                  <Loader2 className="w-4 h-4 animate-spin" />
                ) : (
                  <Send className="w-4 h-4" />
                )}
              </Button>
            </div>

            <div className="flex items-center justify-between mt-3 pt-3 border-t border-border">
              <div className="flex items-center gap-4">
                <Button variant="ghost" size="sm" className="gap-2" disabled={isLoading}>
                  <Paperclip className="w-4 h-4" />
                  Attach
                </Button>
                <Button variant="ghost" size="sm" className="gap-2" disabled={isLoading}>
                  <Mic className="w-4 h-4" />
                  Voice Message
                </Button>
                <Button variant="ghost" size="sm" className="gap-2" disabled={isLoading}>
                  <Search className="w-4 h-4" />
                  Browse Prompts
                </Button>
              </div>
              <span className="text-sm text-muted-foreground">
                {inputValue.length} / 3,000
              </span>
            </div>
          </div>

          <p className="text-xs text-muted-foreground text-center mt-3">
            Netra may generate inaccurate information please confirm first 
          </p>
        </div>
      </div>
    </div>
  )
}